class stack {
	constructor(capacity) {
		this.element = [];
		this.cap = capacity;
	}
	
	push(e) {
		if (e == undefined) {
			console.log('push undefined.');
			return;
		}
		if (this.element.length == this.cap) {
			this.element.shift();
			console.log('超过容量，挤掉栈底')
		}
		this.element.push(e);
		return;
	}
	
	pop() {
		if (this.element.length == 0) return;
		return this.element.pop();
	}
	
	size() {
		return this.element.length;
	}
	
	empty() {
		return this.element.length == 0;
	}
	
	clear() {
		this.element.splice(0, this.element.length);
		this.top = 0;
		return;
	}
}

export {
	stack
}