<template>
	<v-container fluid>
		<v-row>
			<v-col cols="12">
				<v-row align=center justify=center>
					<v-btn-toggle dense borderless background-color="white accent-3">
						<v-btn @click="undo"><!-- 撤销 -->
							<v-icon dark>mdi-undo</v-icon>
						</v-btn>
						
						<v-btn @click="redo"><!-- 重做 -->
							<v-icon dark>mdi-redo</v-icon>
						</v-btn>
						
						<v-btn @click="resetToOrigin"><!-- 恢复原图 -->
							<v-icon dark>mdi-restore</v-icon>
						</v-btn>
					</v-btn-toggle>
				</v-row>
			</v-col>
		</v-row>
		
		<!-- 功能抽屉 -->
		<v-list>
			<v-list-group
				v-for="item in items" :key="item.title"
				:prepend-icon="item.icon" no-action
			>
				<template v-slot:activator>
					<v-list-item-content>
						<v-list-item-title v-text="item.title"></v-list-item-title>
					</v-list-item-content>
				</template>
				
				<v-list-item
					v-for="(subItem, idx) in item.items" :key="subItem.title"
					@click="item.action(idx)"
				>
					<v-list-item-content>
						<v-list-item-title v-text="subItem.title"></v-list-item-title>
					</v-list-item-content>
				</v-list-item>
			
			</v-list-group>
		</v-list>
		
	</v-container>
</template>

<script>
	import { stack } from "../js/stack"
	import { parsing, edit } from "../js/operator"
	export default {
		name: 'basicOperate',
		props: ['canvas', 'origin'], // 画布，原图数据
		data() {
			return {
				// 存更多记录可能需要数据库
				maxRecord: 10,
				undoList: new stack(this.maxRecord),
				redoList: new stack(this.maxRecord),
				items: [
					{
						icon: 'mdi-pencil',
						title: '基础编辑',
						action: this.basicOP,
						items: [
							{ title: '色彩反转'},
							{ title: '去色'},
							{ title: '浮雕'},
						],
					},
					{
						icon: 'mdi-pencil',
						title: '风格编辑',
						items: [
							// { title: '添加滤镜'},
						],
					},
				],
			}
		},
		methods: {
			resetToOrigin () {
				if(typeof(this.origin) == "undefined") return;
				if(confirm("您点击了恢复原图，该操作不可撤销，是否确认？")) {
					edit(this.canvas, this.origin);
					this.undoList.clear();
					this.redoList.clear();
					console.log('undoList:', this.undoList.size());
					console.log('redoList:', this.redoList.size());
				}
			},
			undo () {
				if (this.undoList.empty()) {
					alert('没有可以撤销的操作了');
					return;
				}
				var imgData = this.undoList.pop();
				this.redoList.push(edit(this.canvas, imgData));
				console.log('undoList:', this.undoList.size());
				console.log('redoList:', this.redoList.size());
			},
			redo () {
				if (this.redoList.empty()) {
					alert('没有可以重做的操作了');
					return;
				}
				var imgData = this.redoList.pop();
				this.undoList.push(edit(this.canvas, imgData));
				console.log('undoList:', this.undoList.size());
				console.log('redoList:', this.redoList.size());
			},
			basicOP (n) {
				this.undoList.push(parsing(this.canvas, n));
				console.log('undoList:', this.undoList.size());
			}
		}
	}
</script>

