<template>
  <v-container fluid>
	<!-- 栅格Reference：https://vuetifyjs.com/zh-Hans/components/grids/ -->
	<v-row>
		<v-col cols="12">
			<v-row align= "center" 
				justify="center"
				class="grey lighten-5" 
			>
				<img v-show="false" ref="myImg" src="../assets/default1.jpg" />
				<canvas ref="myCanvas" id="user-img"></canvas>
			</v-row>
			<v-row align= "stretch" justify="center">
				<form enctype="multipart/form-data">
					<v-btn v-on:click="choose" class="ma-2">
						选择图片
					</v-btn>
					<v-btn v-on:click="upload" class="ma-2">
						上传图片
						<input id="img-file" type="file" style="display: none" v-on:change="getUserImg"/>
					</v-btn>
					<v-btn v-on:click="download" class="ma-2">
						保存图片
					</v-btn>
				</form>
			</v-row>
		</v-col>
	</v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'midBlock',
	data() {
		return {
		}
	},
	mounted:function(){
		console.log('进入预设函数');
		var canvas = this.$refs.myCanvas ;
		var img = this.$refs.myImg;
		if(canvas.getContext) {
			console.log('浏览器支持Canvas!');
			var ctx=canvas.getContext("2d");
		}
		else {
			console.log('浏览器不支持Canvas!');
			return;
		}
		img.onload = function(){
			var scale = 0.75;
			canvas.width = Math.min(document.body.clientWidth * scale, img.width);
			canvas.height = Math.min(document.body.clientHeight * scale, img.height);
			var xRate = canvas.width / img.width;
			var yRate = canvas.height / img.height;
			var setRate = xRate < yRate ? xRate : yRate;
			setRate = Math.min(1, setRate); // 允许缩小，不允许放大
			
			// 计算调整后的图片大小
			var dW = img.width * setRate; 
			var dH = img.height * setRate;
			
			// 设置画布大小和图片一致
			canvas.width = dW; 
			canvas.height = dH;
			console.log('绘制预设图像');
			ctx.drawImage(img, 0, 0, dW, dH);
		}
	},
	methods: {
		setDefaultImg () {
			
		},
		upload () {
			document.getElementById("img-file").click();
		},
		download () {
			console.log('图片下载到本地');
			var aTag = document.createElement('a');
			var canvas = document.getElementById('user-img');
			canvas.toBlob(function(blob){
				aTag.download = 'imgFile.jpeg';
				aTag.href = URL.createObjectURL(blob);
				aTag.click();
			}, "image/jpeg", 1.0);
		},
		choose () {
			console.log('选一张默认图片');
		},
		getUserImg: async function () {
			var canvas = await this.preprocess();
			console.log('准备将画布传给父组件', canvas);
			this.$emit('canvasReady', canvas); // 将画布传给父组件
		},
		preprocess () {
			return new Promise((resolve) => {
				var fileInput = document.getElementById('img-file');
				var canvas = document.getElementById('user-img');
				if(canvas.getContext) {
					console.log('浏览器支持Canvas!');
					var ctx = canvas.getContext('2d');
				}
				else {
					console.log('浏览器不支持Canvas!');
					return;
				}
				if (!fileInput.value) {
					return;
				}
				var file = fileInput.files[0]; // 获取File引用
				console.log('清除画布');
				ctx.clearRect(0, 0, canvas.offsetWidth, canvas.offsetHeight);
				var reader = new FileReader();
				// 以DataURL的形式读取文件, 发起一个异步操作来读取文件内容
				reader.readAsDataURL(file);
				reader.onload = function(e) {  // 文件读取完后调用
					var img = new Image();   // 创建img元素
					img.src = e.target.result; // 'data:image/jpeg;base64,/9j/4AAQSk...(base64编码)...'
					img.onload = function(){	// 图片装载完后调用
						var scale = 0.75;
						canvas.width = Math.min(document.body.clientWidth * scale, img.width);
						canvas.height = Math.min(document.body.clientHeight * scale, img.height);
						var xRate = canvas.width / img.width;
						var yRate = canvas.height / img.height;
						var setRate = xRate < yRate ? xRate : yRate;
						setRate = Math.min(1, setRate); // 允许缩小，不允许放大
						
						// 计算调整后的图片大小
						var dW = img.width * setRate; 
						var dH = img.height * setRate;
						
						// 设置画布大小和图片一致
						canvas.width = dW; 
						canvas.height = dH;
						ctx.drawImage(img, 0, 0, dW, dH);
						// var dx = (canvas.width - dW) / 2;
						// var dy = (canvas.height - dH) / 2;
						// ctx.drawImage(img, dx, dy, dW, dH);
						console.log('画布准备返回')
						resolve(canvas);
					}
				}
			});
		}
	},
  }
</script>
